import getSuffixExt from "../../getSuffixExt";
export class AudioValidator {
    validate(name) {
        const rules = AudioValidator.rules;
        const realExt = getSuffixExt(name).toLocaleLowerCase();
        return rules.includes(`.${realExt}`);
    }
}
AudioValidator.rules = [".mpeg", ".mp3", ".mpeg-4", ".midi", ".wma"];
