import getSuffixExt from "../../getSuffixExt";
export class PictureValidator {
    validate(name) {
        const rules = PictureValidator.rules;
        const realExt = getSuffixExt(name).toLocaleLowerCase();
        return rules.includes(`.${realExt}`);
    }
}
PictureValidator.rules = [".bmp", ".jpg", ".jpeg", ".png", ".gif"];
