import getSuffixExt from "../../getSuffixExt";
export class VideoValidator {
    validate(name) {
        const rules = VideoValidator.rules;
        const realExt = getSuffixExt(name).toLocaleLowerCase();
        return rules.includes(`.${realExt}`);
    }
}
VideoValidator.rules = [".wmv", ".mov", ".mpeg", ".mpg", ".rmvb", ".mp4", ".rm", ".avi", ".3gp"];
